.overcome-parent {
  /* Take 100% with of the page thus ignoring the parent width */
  width: 100vw;
  margin-left: calc(50% - 50vw);
  margin-top: -1em;
  margin-bottom: 0.5em;
}

.tab-link {
  text-align: center;
}

.tab-drop {
  padding-top: 1ch;
  background-color: #f1f1f1;
}

/* DANI: El flex no funciona bien en ningun navegador al hacer wrap en columna y no tiene solución.
DANI: Por eso asigno el ancho a mano con un valor que he comprovado experimentalmente que funciona bien
DANI: Y dado que al ancho es fijo también asigno la altura fija para ahorrarme problemas
https://stackoverflow.com/questions/23408539/how-can-i-make-a-displayflex-container-expand-horizontally-with-its-wrapped-con/41209186#41209186 */
.analyses {
  display: flex;
  flex-flow: column wrap;
